  -o-transition:all 0.5s ease-in-out;
  transition:all 0.5s ease-in-out;
  -webkit-animation:blink normal 0.4s 5 ease-in-out;
}

.none {
  display: none;
}

@keyframes blink {
  0% {
         background-color: rgba(225,225,225,1)
  }
  50% {
         background-color: rgba(225,225,225,0.5)
  }
  100% {
         background-color: rgba(225,225,225,1)
  }
}
